import { LanguageService } from './../../../../../services/language.service';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Component, OnInit } from '@angular/core';
import { VhQueryCafe } from 'ionic-vhframeworks';

@Component({
  selector: 'app-coordination',
  templateUrl: './coordination.component.html',
  styleUrls: ['./coordination.component.scss']
})
export class CoordinationComponent implements OnInit {
  //  kitchen_dispatchers_enable: any = this.vhQueryCafe.getLocalAppSettingNameBranch("dispatchers_enable").kitchen_dispatchers_enable; // điều phối bếp
  //  warehouse_dispatchers_enable: any = this.vhQueryCafe.getLocalAppSettingNameBranch("dispatchers_enable").warehouse_dispatchers_enable;// điều phối nguyên liệu
  //  service_dispatchers_enable: any = this.vhQueryCafe.getLocalAppSettingNameBranch("dispatchers_enable").service_dispatchers_enable;// điều phối dịch vụ
  dispatchers_enable: any = this.vhQueryCafe.getLocalAppSettingNameBranch("dispatchers_enable").dispatchers_enable ? 1 : 0;
  open_dialog_print_kitchen_bill_detail: any = this.vhQueryCafe.getLocalAppSettingNameBranch("dispatchers_enable").open_dialog_print_kitchen_bill_detail ;
  constructor(
    private vhQueryCafe: VhQueryCafe,
    private mess: NzMessageService,
    private ls: LanguageService
  ) { }

  ngOnInit(): void {
  }
  updateSetting(title: string, event) {
    let value ={};
    value[title] = event ?  true : false
    
    this.vhQueryCafe.updateAppSettingNameBranch('dispatchers_enable',value).then((bool)=>{
      if(!bool) this.mess.error(this.ls.translate("An error while updating coordination"))
      })
  }
}
