import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ConfigurationComponent } from './configuration.component';
import { CurrencyComponent } from './currency/currency.component';
import { DisplayComponent } from './display/display.component'; 
import { Routes, RouterModule } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CoordinationComponent } from './coordination/coordination.component';
import { ProductsComponent } from './products/products.component';
import { SalesProgramComponent } from './sales-program/sales-program.component';
import { ReportComponent } from './report/report.component';
import { SalesComponent } from './sales/sales.component';
import { LedPriceComponent } from './led-price/led-price.component';
const routes: Routes = [
  {
    path: '',
    component: ConfigurationComponent
  },
  {
    path: 'display',
    component: DisplayComponent
  },
  {
    path: 'coordination',
    component: CoordinationComponent
  },
  {
    path: 'products',
    component: ProductsComponent
  },
  {
    path: 'sales-program',
    component: SalesProgramComponent
  },
  {
    path: 'led-price',
    component: LedPriceComponent
  },
  {
    path: 'report',
    component: ReportComponent
  },
]

@NgModule({
  declarations: [ConfigurationComponent, CurrencyComponent, DisplayComponent, SalesProgramComponent,
    ReportComponent, SalesComponent, CoordinationComponent, ProductsComponent, LedPriceComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule,
  ]
})
export class ConfigurationModule { }
