import { VhAuth } from 'ionic-vhframeworks';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-sales',
  templateUrl: './sales.component.html',
  styleUrls: ['./sales.component.scss']
})
export class SalesComponent {
  public sales: any = JSON.parse(localStorage.getItem("vhsales-sales"));
  constructor(
    private vhAuth:VhAuth
  ) { }

  updateSetting(title: string, event) {
    this.sales[title] = event;
    localStorage.setItem("vhsales-sales", JSON.stringify(this.sales))
  }


}
