 
import { VhQueryCafe, VhDisplayLEDService } from 'ionic-vhframeworks';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Platform } from '@ionic/angular';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
declare var electron: any;
@Component({
  selector: 'app-led-price',
  templateUrl: './led-price.component.html',
  styleUrls: ['./led-price.component.scss']
})
export class LedPriceComponent implements OnInit {
  public displayLEDs: any[] = [];                           // danh sách màn hình LED
  comGates: any = [];
  data: any = ""
  status: boolean = true
  baund_current: any = 2400
  baundrates = [600, 1200, 2400, 4800, 9600, 14400, 19200, 38400, 56000, 57600, 115200, 128000];
  public isOpenAddLed = false;                                // Ẩn/ hiện popup thêm led
  public display_modes: any = [];
  public display_models: any = [];
  public databits: any = [5, 6, 7, 8];
  public parities: any = ['None', 'Even', 'Odd', 'Mark', 'Space'];
  public stopbits: any = [1, 2];
  public flow_controls: any = ['None', 'CTS', 'RTS', 'XON', 'XOFF']
  public formAddLED: FormGroup;                               // Form thêm màn hình led
  public formEditLED: FormGroup;                              // Form sửa màn hình led
  public isOpenEditLED = false;                               // Ẩn/ hiển popup sửa màn hình led
  constructor(
    public platform: Platform,
    public vhQueryCafe: VhQueryCafe,
    public vhDisplayLEDService: VhDisplayLEDService,
    public vhComponent: VhComponent,
    public languageService: LanguageService
  ) {
    if (this.platform.is("electron")) this.scan();
  }
  ngOnInit(): void {
    this.display_models = this.vhDisplayLEDService.getlocalDisplayModels()
    this.getData();
  }
  scan() {
    this.vhDisplayLEDService.scanPorts().then((data) => {
      this.comGates = data
    })
  }
  getData() {
    this.vhDisplayLEDService.refreshLocalDisplayLEDs().then(() => {
      this.displayLEDs = this.vhDisplayLEDService.getlocalDisplayLEDs().map(item => {
        let display_models = this.display_models.find(mode => mode.type == item.type_display_mode)
        return {
          ...item,
          mode_name: display_models.modes.find(f => f.type == item.type_display_mode).name,
          model_name: this.display_models.find(f => f.type == item.type_display_model).name
        }
      })
    })
  }
  selectModel(type, event) {
    if (type == 'add') this.formAddLED.controls['type_display_mode'].setValue(null)
    else this.formEditLED.controls['type_display_mode'].setValue(null)
    this.display_modes = this.display_models.find(item => item.type == event).modes
  }
  // Khởi tạo form thêm LED
  initFormAddLed() {
    this.formAddLED = new FormGroup({
      name: new FormControl("", [Validators.required]),
      type_display_mode: new FormControl(null, [Validators.required]),
      type_display_model: new FormControl(null, [Validators.required]),
      port: new FormControl("",[Validators.required]),
      baundrate: new FormControl(9600, [Validators.required]),
      databits: new FormControl(8, [Validators.required]),
      parity: new FormControl("None", [Validators.required]),
      stopbits: new FormControl(1, [Validators.required]),
      flow_control: new FormControl("None", [Validators.required]),
      enable: new FormControl(false, [Validators.required]),
    })
    this.isOpenAddLed = true;
  }
  // Khởi tạo form sửa LED
  initFormEditLed(item) {
    this.formEditLED = new FormGroup({
      _id: new FormControl(item._id, [Validators.required]),
      name: new FormControl(item.name, [Validators.required]),
      type_display_mode: new FormControl(item.type_display_mode, [Validators.required]),
      type_display_model: new FormControl(item.type_display_model, [Validators.required]),
      port: new FormControl(item.port),
      baundrate: new FormControl(item.baundrate, [Validators.required]),
      databits: new FormControl(item.databits, [Validators.required]),
      parity: new FormControl(item.parity, [Validators.required]),
      stopbits: new FormControl(item.stopbits, [Validators.required]),
      flow_control: new FormControl(item.flow_control, [Validators.required]),
      enable: new FormControl(item.enable, [Validators.required]),
    })
  }

  // Thêm LED
  saveLED(data) {
    if (this.formAddLED.valid) {
      this.vhDisplayLEDService.addDisplayLED(data).then((displayLED: any) => {
        if (displayLED) {
          displayLED = {
            ...displayLED,
            mode_name: this.display_modes.find(f => f.type == displayLED.type_display_mode).name,
            model_name: this.display_models.find(f => f.type == displayLED.type_display_model).name
          }
          this.displayLEDs = [...this.displayLEDs, displayLED]
          this.isOpenAddLed = false;
          this.vhComponent.showToast(3000, displayLED.name + " " + (this.languageService.translate("Has been added successfully")), "success-toast");
        }
      }, error => {
        this.isOpenAddLed = false;
        this.vhComponent.showToast(3000, error, "alert-toast");
      })
    }
  }

  openEditDisplayLED(item) {
    this.display_modes = this.display_models.find(mode => mode.type == item.type_display_mode).modes
    this.initFormEditLed(item);
    this.isOpenEditLED = true;
  }

  updateEnable(id, value){
    this.vhDisplayLEDService.updateDisplayLED(id, {enable : value}).then((bool) => {
      if (bool) {
        this.vhComponent.showToast(3000, "Lưu thành công", "success-toast");
      }
    }, error => {
      this.vhComponent.showToast(3000, error, "alert-toast");
      this.isOpenEditLED = false;
    });
  }

  saveEditLED(item) {
    if (this.formEditLED.valid) {
      this.vhDisplayLEDService.updateDisplayLED(item._id, item).then((bool) => {
        if (bool) {
          item = {
            ...item,
            mode_name: this.display_modes.find(f => f.type == item.type_display_mode).name,
            model_name: this.display_models.find(f => f.type == item.type_display_model).name
          }
          this.displayLEDs = this.displayLEDs.filter(f => f._id != item._id);
          this.displayLEDs = [...this.displayLEDs, item]
          this.isOpenEditLED = false;
          this.vhComponent.showToast(3000, "Lưu thành công", "success-toast");
        }
      }, error => {
        this.vhComponent.showToast(3000, error, "alert-toast");
        this.isOpenEditLED = false;
      });
    }
  }

  deleteLED(item) {
    this.vhDisplayLEDService.deleteDisplayLED(item._id).then((bool) => {
      if (bool) {
        this.displayLEDs = this.displayLEDs.filter(f => f._id != item._id);
      }
    }, error => {
      this.vhComponent.showToast(3000, error, "alert-toast");
    })
  }
}
