import { Component } from '@angular/core';
import { VhQueryCafe } from 'ionic-vhframeworks';

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.scss']
})
export class ReportComponent {
  // doanh số danh mục report_sales_category
  public show_btax_report_sales_category: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_category;
  public show_image_report_sales_category: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_image_report_sales_category;

  // doanh số KH report_sales_customer
  public show_btax_report_sales_customer: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_customer;

  //  doanh số  NV report_sales_employee
  public show_btax_report_sales_employee: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_employee;

  //  doanh số  SP report_sales_product
  public show_btax_report_sales_product: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_product;

  //  doanh số  dịch vụ report_sales_service
  public show_btax_report_sales_service: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_service;

  //  doanh số  dịch vụ report_sales_combo
  public show_btax_report_sales_combo: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_combo;

  //nhập xuất tồn input_output_inventory
  public show_btax_report_input_output_inventory: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_input_output_inventory;

  //nhập xuất trả input_output_return
  public show_btax_report_input_output_return: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_input_output_return;
  //tồn kho warehouse
  public show_btax_report_warehouse: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_warehouse;

  //doanh  so khu vuc
  show_btax_report_sales_area: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_area;
  //doanh  so bàn
  show_btax_report_sales_table: any = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_table;
  constructor(
    private vhQueryCafe: VhQueryCafe
  ) { }

  ngOnInit(): void {
  }

  updateSetting(value) {
    this.vhQueryCafe.updateAppSettingNameBranch('permission_branch', value).then((bool) => {
      console.log(bool);
    })
  }

}
