import { Component, OnInit } from '@angular/core';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';
@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss']
})
export class ProductsComponent implements OnInit {
 
  display_promotion_selling_price: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  auto_select_lot : boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').auto_select_lot
  auto_hidden_lot: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').auto_hidden_lot
  warning_lot_expried : boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').warning_lot_expried
  management_product_manysize: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').management_product_manysize
  management_product_lot: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').management_product_lot
  management_product_price2: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').management_product_price2
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    public lang: LanguageService,
  ) {
  }
  ngOnInit(): void {
   
  }
  updateShowTypePrice(value) {
    this.vhQueryCafe.updateAppSettingNameBranch('permission_branch', value).then((bool) => {
      console.log(bool);
    })
  }

}
