import { LanguageService } from './../../../../../services/language.service';
import { Component, OnInit, } from '@angular/core';
import { VhAuth } from 'ionic-vhframeworks';

@Component({
  selector: 'app-display',
  templateUrl: './display.component.html',
  styleUrls: ['./display.component.scss']
})
export class DisplayComponent implements OnInit {
  size: any = [12, 13, 14, 15, 16, 17, 18, 19, 20];
  interface: any = [
    { id: 'overview', name: this.languageService.translate("Overviews") },
    { id: 'pos', name: this.languageService.translate("Order pos") }
  ]
  constructor(public vhAuth: VhAuth,
    private languageService: LanguageService) { }

  ngOnInit() {
    //this.vhAuth.changeThemeIcon();
  }
  ngAfterViewInit() {

    if (this.vhAuth.getThemeValue())
      this.vhAuth.changeThemeIcon();

  }
  /////////////------------------------FONT_SIZE---------------------------//////////////
  public fontSize: number = parseFloat(localStorage.getItem("vh-cafe-system-fontsize"));
  public setFontSize(value) {
    localStorage.setItem("vh-cafe-system-fontsize", JSON.stringify(value))
    this.fontSize = value
    document.documentElement.style.fontSize = value + 'px'
  }
  /////////////------------------------COLOR---------------------------//////////////
  changeColor(theme, event) {
    this.vhAuth.changeTheme(theme, event)
  }

  /////////////------------------------LOGIN INTERFACE---------------------------//////////////
  public startInterface: string = localStorage.getItem("vh-cafe-system-start-interface");
  public setInterface(value) {
    localStorage.setItem("vh-cafe-system-start-interface", value)
    this.startInterface = value

  }

}
